@import './../../styles/color';

.create-options-box{
    max-width: 400px;
    width: 80vw;
    text-align: center;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
    align-items: flex-start;

}

.create-options-box p{
    padding: 0;
}
.create-options-box h5{
    margin-bottom: 10px;
}

.photouser-box{
    width: 150px;
    height: 150px;
    background-size: cover;
    border-radius: 10px;
    opacity: 0.6;
    margin-bottom: 30px;
    margin-top: 10px;
    transition: 0.7s;
}

.photouser-box:hover{
    opacity: 1;
}
.photouser-box div input{
    width: 100%;
    height: 100%;
    opacity: 0;
}

.photouser-box div {
    background-repeat: no-repeat;
    background-position: center;
    height: 100%;
    background-size: 60px;
}



.city-img{
    width: 500px;
    height: 200px;
    background-size: cover;
    border-radius: 10px;
    max-width: 85vw;
    margin: 32px 0;
}

.city-country-flag{
    width: 30px;
    height: 20px;
    background-size: cover;
    margin-left: 10px;
}

.city-country{
    display: flex;
    justify-content: center;
    align-items: center;
}
.city-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: -20px;
}


.create-options-box h2{
    margin: 0;

}


.question-citys button{
    width: 300px;
    background: none;
    height: 40px;
    border-radius: 10px;
    margin-bottom: 5px;
    opacity: 0.7;
    align-self: center;
}

.question-citys button:hover{
    opacity: 1!important;
}
.question-citys {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.question-citys span{
    display: flex;
    margin: 32px 0;

    flex-direction: column;
    
    p{
        margin: 32px 0;

    }
}
.question-citys span button:hover{
    box-shadow: 0 0.3rem 0.6rem rgb(31 52 62 / 16%);
    transform: translate(0.2rem,-0.2rem);}



.button-option01{
    border: solid 2px $terciary-color;
    color: var(--3);
    transition: 0.4s;
}

.button-option02{
    border: solid 2px $secondary-color;
    color: var(--2);
    transition: 0.4s;

}

.button-option03{
    border: solid 2px $primary-color;
    color: var(--1);
    transition: 0.4s;

}
.city-description{
    margin: 32px 0;
}



