.blog-article-box{
    width: 310px;
    height: 330px;
    overflow: hidden;
    background-color: var(--9);
    border-radius: 4px;
    margin-bottom: 30px;

}
.blog-article-container{
    padding: 0px 20px;
    margin-top: -10px;
    display: flex;
    flex-direction: column;
    height: 199px;
    justify-content: space-between;
    color: (var--7);
}
.blog-article-container p{
    font-size: 14px;
    color: var(--7);
}
.article-title{
    margin: 0;
    margin-top: 10px;
}

.article-poster{
    height: 130px;
    width: 300px;
    background-size: cover;
    border-radius: 2px;
    margin-left: 5px;
    margin-top: 5px;
}

.other-articles{
    display: flex;
    width: 1100px;
    max-width: 95%!important;
    flex-wrap: wrap;
    margin-top: 40px;
    justify-content: center;
    gap: 15px;
    margin-left: 100px;
}





.princial-article{
    width: 1100px;
    max-width: 86%!important;
    border-radius: 4px;
    background-color: var(--9);
    display: flex;
    margin-left: 100px;
}

.princial-article-img{
    width: 40%;
    height: 300px;
    
}

.princial-article-img div{
    border-radius: 4px;
    width: 400px!important;
    height: 100%;
    max-width: 100%;


    background-size: cover;
}

.principal-article-container{
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between!important;
    width: 60%;
    align-items: flex-start!important;
    height: 290px;
    overflow: hidden;
    margin-right: 10px;
    

}
.princial-article-category{
    margin-bottom: -10px;
    color: var(--2);
}




.princial-article-title{
    margin: 0;

}

.blog-articles-div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.principal-article-description{
    color: var(--7);
    margin: 0;
}
.blog-btn-controller{
    margin: 0 auto;
    text-align: center;
    margin-bottom: 70px;
}

.blog-btn-controller button{
    background: none;
    border: none;
    color: var(--7);
}
.princial-article-time{
    font-size: 12px;
}

.article-time{
        justify-content: left;
}

.article-time img{
    margin-right: 3px;
}