.home {
  display: grid;
  gap: 10px;
}

.city-div {
  grid-column-start: 0;
  grid-column-end: 1;
  grid-row-start: 0;
  grid-row-end: 1;
}

.blog-div {
  grid-column-start: 0;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 3;
}

.radio-box {
  grid-column-start: 0;
  grid-column-end: 1;
  grid-row-start: 3;
  grid-row-end: 4;
}

.todolist-box {
  grid-column-start: 0;
  grid-column-end: 2;
  grid-row-start: 0;
  grid-row-end: 1;
}/*# sourceMappingURL=home.css.map */